.musicVolume {
    width: 600px;
    margin-top: 20px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.musicVolume::-webkit-slider-thumb {
    background: #ecf0f1;
    border: 5px solid rgb(0, 0, 0);
    cursor: pointer;
    width: 15px;
    height: 15px;
    transform: scaleY(3);
    -webkit-appearance: none;
}

.musicVolume::-webkit-slider-runnable-track {
    height: 15px;
    outline: 5px solid rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
}

